*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.638);
}
.cards{
    display: grid;
    grid-template-columns: auto auto auto;
    background-color: rgba(162, 36, 36, 0.91);
    margin: 20px;
    .basic{
        // height: 50vh;
        padding: 50px 20px;
        border-radius: 20px;
        gap: 20px;
        background-color: blueviolet;
        margin: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
}
.nav{
    display: flex;
    height: 10vh;
    background-color: rgba(0, 0, 0, 0.641);
    justify-content: space-between;
    padding: 0 100px;
    align-items: center;
    box-shadow: 2px 2px rgb(108, 9, 9);
    .logo{
        color: white;
        font-size: 60px;
        &:hover{
            color: red;
        }
    }
    input{
        padding: 10px 60px ;
        font-size: 15px;
    }
}